import React, { Component } from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { PropTypes } from "prop-types";
import { toggleSlide } from "../../redux/actions/ui.actions";
class Sidebar extends Component {
  handleClose = () => {
    this.props.toggleSlide();
  };
  render() {
    let asideClasses = "aside";
    if (this.props.ui.sidebarOpen) {
      asideClasses += " open";
    }
    return (
      <aside className={asideClasses}>
        <div className="aside__header">
          <div className="logo">
            <img
              src="data:image/jpeg;base64,"
              alt=""
            />
          </div>
          <div className="close" onClick={this.handleClose}>
            <i className="fa fa-window-close" />
          </div>
        </div>
        {this.props.user.loggedIn ? (
          <ul>
            <li>
              <Link to="/messages/new">New Message</Link>
            </li>
            <li>
              <Link to="/inbox">Inbox</Link>
            </li>
            <li>
              <Link to="/messages/sent">Sent Mails</Link>
            </li>
            <li>
              <Link to="/messages/draft">Drafts</Link>
            </li>
            <li>
              <Link to="/groups">Groups</Link>
            </li>
            <li>
              <Link to="/contacts">Contacts</Link>
            </li>
            <li>
              <Link to="/profile">Profile</Link>
            </li>
          </ul>
        ) : (
          <ul>
            <li>
              <Link to="/auth/login">Login</Link>
            </li>
            <li>
              <Link to="/auth/register">Register</Link>
            </li>
            <li>
              <Link to="/about">About</Link>
            </li>
          </ul>
        )}
      </aside>
    );
  }
}

const mapStateToProps = state => ({
  ui: state.ui,
  user: state.user
});

Sidebar.propTypes = {
  toggleSlide: PropTypes.func.isRequired
};

export default connect(
  mapStateToProps,
  { toggleSlide }
)(Sidebar);
